.wrapper {
  padding: 40px 0;
  .video {
    width: 100%;
    border-radius: 8px;
  }
  .aboutUsDescription {
    padding: 20px 0;
    background: #ECECEC;
  }
  .teamMembersWrapper {
    .teamMembersHeader {
      margin: 0 auto;
    }
    .teamMembers {
      > div {
        flex: 1 1 250px;
        display: flex;
        align-items: flex-end;
        text-align: center;
        justify-content: center;
        min-height: 300px;
        max-width: 280px;
        width: 100%;
        background-position: top;
        background-repeat: no-repeat;
        border-radius: 10px;
        transition: transform 0.4s ease-in-out;
        &:hover {
          transform: scale(1.1);
        }
        .memberInfo {
          padding: 10px;
          background: rgb(255, 255, 255, 0.6);
          border-top-left-radius: 8px;
          border-top-right-radius: 8px;
          margin: 0 20px;
        }
      }
    }
  }
}
